import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-data-binding',
  templateUrl: './data-binding.component.html',
  styleUrls: ['./data-binding.component.css']
})
export class DataBindingComponent implements OnInit {

  message:string = 'Good Morning';
  imgSource:string = 'https://images.pexels.com/photos/736716/pexels-photo-736716.jpeg?auto=compress&cs=tinysrgb&dpr=1&w=500';
  greeting:string = null;
  count:number = null;
  username:string = null;

  constructor() { }

  ngOnInit(): void {
  }

  // get Greeting
  public getGreeting():void{
    (this.count === null)? this.count = 1: this.count++;
    this.greeting = 'You clicked the button';
  }

  // getUserData
  public getUserData(input:HTMLInputElement):void{
    this.username = input.value;
  }

}
